.top-header-hr {
  height: 4px;
  margin: 0;
  background-color: #047cc0;
  width: 100%;
  border: none;
  flex: none;
}

.top-hr:hover {
  background-color: #009bef;
}

.bottom-header {}

.bottom-header-dark {
  background-color: #1a354d;
}

.bottom-header-hr {
