  height: 1px;
  background-color: #bdbabd;
  border: none;
  flex: none;
}

.bottom-header-hr-dark {
  background-color: #516577;
}

.popup-window-header-area {
  margin: 0;
  padding: 0;
  display: flex;
  flex: none;
  height: 50px;
  color: #777677;
}

.popup-window-header-area-dark {
  background-color: #1a354d;
  color: #C0BFC0;
}

.popup-window-header {
  margin-left: 2rem;
  font-size: 21px;
  float: left;
  flex: 1 1 auto;
  display: flex;
  align-items: center;
}

.close-button {
  height: 20px;
  width: 20px;
  margin: 10px;
  padding: 0;
  flex: none;
  box-sizing: border-box;
  background-color: transparent;
