  .popup-window-popup {
    width: 95%;
    margin: 1em auto;
  }
}

.popup-window-content {
  flex: 1 1 auto;
  overflow-y: auto;
  overflow-x: hidden;
  display: flex;
  flex-direction: column;
}

.popup-window-button {
  font-size: large;
