}

.info .leftPanel {
  background-color: rgba(0, 182, 203, 1);
}

.warning .severityIcon {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
}

.info .severityIcon {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_2___});
}

.title {
  border-bottom: 1px solid;
  font-kerning: normal;
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: flex-end;
}

.titleText {
  font-size: 150%;
  font-size:21px;
}

.error .title {
  border-bottom-color: #AA231F;
  color: #AA231F;
}

.warning .title {
  border-bottom-color: #e99023;
  color: #e99023;
}

.info .title {
  border-bottom-color: #188291;
