.popup-shadow-hover {
  background-color: rgba(0, 0, 0, 0.4);
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  z-index: 100;
}

.hidden {
  display: none;
}

.attachToCenter {
  min-width: 400px;
  display: inline-block;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.attachToCorner {
  min-width: 400px;
  max-width: 650px;
  position: absolute;
  right: 20px;
  bottom: 20px;
}


